<template>
  <div class="sys-notice-wrap">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item to="/system/notice"></el-breadcrumb-item>
      <el-breadcrumb-item >{{$route.name=='sysEditNotice'?'':''}}</el-breadcrumb-item>
    </el-breadcrumb>
    
    <el-alert
      title=""
      type="info"
      show-icon>
      <div>
        
      </div>
    </el-alert>
    <el-row class="mbt20">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="region">
          <el-select v-model="ruleForm.menuId" placeholder="">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
            <el-option label="" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="    " prop="title">
          <el-input v-model="ruleForm.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="adminName">
          <el-input v-model="ruleForm.adminName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
         <pre>
           <quill-editor
               ref="myTextEditor"
               v-model="ruleForm.content"
               :options="editorOption"
               @change="onEditorChange($event)"
               @ready="onEditorReady($event)">
           </quill-editor>
         </pre>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
         
        </el-form-item>
      </el-form>
    </el-row>
  </div>
</template>
<script type="text/ecmascript-6">
  export default{
    data() {
      var validateContent = (rule, value, callback) => {
        if (this.$http.trim(value).length < 1) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        ruleForm: {
          adminName: '', // 
          title: '', // 
          menuId: '',
//          date1: new Date(), //
          delivery: false,
          type: [],
          order: 1,
          content: '' // 
        },
        editorOption: {
          modules: {
            toolbar: [
              ['bold', 'italic', 'underline', 'strike'],
              ['blockquote', 'code-block'],
              [{ 'header': 1 }, { 'header': 2 }],
              [{ 'list': 'ordered' }, { 'list': 'bullet' }],
              [{ 'script': 'sub' }, { 'script': 'super' }],
              [{ 'indent': '-1' }, { 'indent': '+1' }],
              [{ 'direction': 'rtl' }],
              [{ 'size': ['small', false, 'large', 'huge'] }],
              [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
              [{ 'font': [] }],
              [{ 'color': [] }, { 'background': [] }],
              [{ 'align': [] }],
              ['clean'],
              ['link', 'image']
            ]
          },
          keyboard: {
            bindings: {
              tab: {
                key: 9,
                handler: function(val) {
                }
              },
              custom: {
                key: 'B',
                shiftKey: true,
                handler: function(range, context) {
                }
              }
            }
          }
        },
        editKey: null,
        layedit: null,
        rules: {
          menuId: { required: true, message: '', trigger: 'blur' },
          title: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, message: '3', trigger: 'blur' }
          ],
          adminName: [
            { required: true, message: '', trigger: 'blur' }
          ],
          type: [
            { type: 'array', required: true, message: '', trigger: 'change' }
          ],
          content: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
  },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          let url = '/admin/addPublishanotice'
          if (this.$route.name === 'sysEditNotice') {
            url = '/admin/updatePublishanotice'
          }
          if (valid) {
            this.$ajax(url, this.ruleForm, res => {
              if (res.returnCode === 200) {
                this.$message({ message: res.msg, type: 'success' })
                if (this.$route.name === 'sysEditNotice') {
                  this.getNoticeById()
                }
              }
            })
          } else {
            this.$message({ message: '', type: 'warning' })
            return false
          }
        })
      },
  
      getNoticeById() {
        this.$ajax('/admin/sys-getNoticeById', {
          noticeid: this.$route.params.id
        }, res => {
          if (res.returnCode === 200) {
            delete res.data.releaseDate
            this.ruleForm = res.data
          }
        }, 'get')
      },
      resetForm() {
        this.getNoticeById()
      },
      onEditorChange(editor, html, text) {
//
      },
      onEditorReady(editor) {
//
      }
    },
    created() {
      if (this.$route.name === 'sysEditNotice') {
        this.getNoticeById()
      }
    }
  
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
.sys-notice-wrap
  pre
    line-height 0
.ql-editor
  height 600px!important
.ql-toolbar
  line-height 1.2
  
.ql-toolbar
  .ql-picker
    &.ql-expanded
      .ql-picker-options
        margin-top 1px!important
</style>



