<template>
  <div class="sys-notice-wrap">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item to="/message/system"></el-breadcrumb-item>
      <el-breadcrumb-item to="/message/system"></el-breadcrumb-item>
      <el-breadcrumb-item >{{$route.name=='mesEditMessage'?'':''}}</el-breadcrumb-item>
    </el-breadcrumb>
    <el-alert
      title=""
      type="info"
      show-icon>
      <div>
        
      </div>
    </el-alert>
    <el-row class="mbt20">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="    " prop="title">
          <el-input v-model="ruleForm.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="userName">
          <el-input v-model="ruleForm.userName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="messageContent">
         <el-input class="mc-wrap" type="textarea" :rows="5" v-model="ruleForm.messageContent"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-row>
  </div>
</template>
<script type="text/ecmascript-6">
  export default{
    data() {
      const validateContent = (rule, value, callback) => {
        if (this.$http.trim(value).length < 1) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        ruleForm: {
          userId: '',
          userName: '', // 
          title: '', // 
          messageContent: '' // 
        },
        rules: {
          title: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, message: '3', trigger: 'blur' }
          ],
          userName: [
            { required: true, message: '', trigger: 'blur' }
          ],
          messageContent: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
  },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          let url = '/admin/sys-addsMessage'
          if (this.$route.name === 'mesEditMessage') {
            url = '/admin/updatePublishanotice'
          }
          if (valid) {
            const copy = JSON.parse(JSON.stringify(this.ruleForm))
            copy.messageContent = copy.messageContent.replace(/ +|+/g, '')
            this.$ajax(url, copy, res => {
              if (res.returnCode === 200) {
                this.$message({ message: res.msg, type: 'success' })
                if (this.$route.name === 'mesEditMessage') {
                  this.getMessageById()
                }
              }
            })
          } else {
            this.$message({ message: '', type: 'warning' })
            return false
          }
        })
      },
      getMessageById() {
        this.$ajax('/admin/sys-getMessageById', {
          noticeid: this.$route.params.id
        }, res => {
          if (res.returnCode === 200) {
            delete res.data.releaseDate
            this.ruleForm = res.data
          }
        }, 'get')
      },
      resetForm() {
        this.getMessageById()
      }
    },
    created() {
      this.ruleForm.userId = this.$store.state.userInfo.adminInfo.userId
      if (this.$route.name === 'mesEditMessage') {
        this.getMessageById()
      }
    },
    watch: {
      'ruleForm.messageContent': function(val) {
        this.ruleForm.messageContent = val.replace(/\s*\n\s*/g, '\n')
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .sys-notice-wrap
    .mc-wrap
      >textarea
        text-indent 2em
    pre
      line-height 0
  .ql-editor
    height 600px!important
  .ql-toolbar
    line-height 1.2
  
  .ql-toolbar
    .ql-picker
      &.ql-expanded
        .ql-picker-options
          margin-top 1px!important
  
</style>



