<template>
    <div class="book-property-wrap">
      <el-alert
        title=""
        type="info"
        show-icon>
        <div>
          <p>
            
          </p>
          <p>
            <span class="red"></span>
          </p>
          <p>
            <span></span>1.414X18070X70
          </p>
        </div>
      </el-alert>
      
      <el-row class="mbt20 book-property-list">
        <h2 class="tab-title"></h2>
        <el-button
          v-for="item in baseInfo.booklablesList"
          :key="item.id"
          :style="{borderColor:item.bookColor,color:item.bookColor}"
          size="small"
          @click="handleEdit('label',item)"
        >{{item.bookLableName}}</el-button>
        <el-button size="small" @click="handleEdit('label')" >+ </el-button>
      </el-row>
      
      <el-row class="mbt20 book-property-list">
        <h2 class="tab-title"></h2>
        <el-button
          v-for="item in baseInfo.classificationList"
          :key="item.id"
          size="small"
          type="primary"
          plain
          @click="handleEdit('class',item)"
        >{{item.classificationName}}</el-button>
        
        <el-button size="small" @click="handleEdit('class')">+ </el-button>
      </el-row>
      <!---->
      
      <el-dialog
        class="alertDialog"
        title=""
        :visible.sync="dialogVisible">
        <el-form :model="subData" ref="propertyForm" :rules="rules" label-width="66px" class="property-ruleForm">
          <template v-if="currentType==='label'">
            <el-form-item label="  " prop="bookColor">
              <el-color-picker v-model="subData.bookColor"></el-color-picker>
            </el-form-item>
            <el-form-item label="" prop="bookLableName">
              <el-input type="text" v-model="subData.bookLableName" placeholder=""></el-input>
            </el-form-item>
          </template>
          <template v-else>
            <el-form-item label="  ">
              <img class="class-icon" v-if="subData.classificationmaxlco" :src="subData.classificationmaxlco" alt="">
              <el-upload
                v-if="dialogType=='edit'"
                class="upload-demo"
                :show-file-list="false"
                :on-success="uploadEnd1"
                :on-error="uploadEnd1"
                :on-progress="uploadProgress1"
                action="/api/admin/updateClassification"
                :data="{id:subData.id,type:1}">
                <el-button size="small" type="primary">
                  <span v-if="load.max"></span>
                  <span v-else><i class="el-icon-loading"></i> ...</span>
                </el-button>
              </el-upload>
  
              <img class="class-icon" v-if="subData.classificationIco" :src="subData.classificationIco" alt="">
              <el-upload
                v-if="dialogType=='edit'"
                class="upload-demo"
                :show-file-list="false"
                :on-success="uploadEnd2"
                :on-error="uploadEnd2"
                :on-progress="uploadProgress2"
                action="/api/admin/updateClassification"
                :data="{id:subData.id,type:2}">
                <el-button size="small" type="primary">
                  <span v-if="load.min"></span>
                  <span v-else><i class="el-icon-loading"></i> ...</span>
                </el-button>
              </el-upload>
              
            </el-form-item>
            <el-form-item label="" prop="orders">
              <el-input type="text" v-model="subData.orders" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="classificationName">
              <el-input type="text" v-model="subData.classificationName" placeholder=""></el-input>
            </el-form-item>
          </template>
        </el-form>
        
        <span slot="footer" class="dialog-footer">
          <template v-if="dialogType==='edit'">
            <el-button type="danger" @click="handleDel('del')"> </el-button>
            <el-button type="primary" @click="handleDel('update')"> </el-button>
          </template>
          <el-button v-else type="primary" @click="handleDel('add')"> </el-button>
        </span>
      </el-dialog>
      
    </div>
</template>

<script>
  export default {
    data() {
      return {
        dialogType: 'edit',
        currentType: 'label',
        baseInfo: {},
        load: {
          max: true,
          min: true
        },
        dialogVisible: false,
        subData: {
          bookColor: '',
          bookLableName: ''
        },
        rules: {
          bookColor: [
            { required: true, message: '', trigger: 'blur' }
          ],
          bookLableName: [
            { required: true, message: '', trigger: 'blur' }
          ],
          classificationName: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      getBookBaseInfo() {
        this.$ajax('/book-EditBookEcho', '', res => {
          if (res.returnCode === 200) {
            this.baseInfo = res.data
          }
        }, 'get')
      },
      handleEdit(type, item) {
        this.dialogVisible = true
        this.currentType = type
        this.$nextTick(() => {
          if (item) {
            this.dialogType = 'edit'
            this.subData = JSON.parse(JSON.stringify(item))
          } else {
            this.dialogType = 'add'
            if (type === 'label') {
              this.subData = {
                bookColor: '',
                bookLableName: ''
              }
            } else {
              this.subData = {
                classificationName: '',
                classificationmaxlco: '',
                classificationIco: '',
                orders: this.baseInfo.classificationList.length + 1
              }
            }
          }
        })
      },
      handleDel(type) {
        const edit = (url, id) => {
          this.$refs['propertyForm'].validate((valid) => {
            if (valid) {
              this.$myLoad(type === 'del' ? '...' : '...')
              this.$ajax(url, id, res => {
                this.dialogVisible = false
                this.$loading().close()
                if (res.returnCode === 200) {
                  this.$message({ message: type === 'del' ? '' : '', type: 'success' })
                  this.getBookBaseInfo()
                }
              })
            }
          })
        }

        if (this.currentType === 'label') {
          if (type === 'del') {
            edit('/admin/deleteAttributeLable', { id: this.subData.id })
          } else if (type === 'update') {
            edit('/admin/updateAttributeLable', this.subData)
          } else {
            edit('/admin/addAttributeLable', this.subData)
          }
        } else if (this.currentType === 'class') {
          if (type === 'del') {
            edit('/admin/deleteClassification', { id: this.subData.id })
          } else if (type === 'update') {
            edit('/admin/updateClassificationData', this.subData)
          } else {
            edit('/admin/addClassification', this.subData)
          }
        }
      },
      uploadProgress1(event) {
        console.log(event)
        this.load.max = false
      },
      uploadEnd1(res) {
        this.load.max = true
        if (res.returnCode === 200) {
          this.subData.classificationmaxlco = res.data
          this.getBookBaseInfo()
        } else {
          this.$message({ message: res.msg, type: 'warning' })
        }
      },
      uploadProgress2() {
        this.load.min = false
      },
      uploadEnd2(res) {
        this.load.min = true
        if (res.returnCode === 200) {
          this.subData.classificationIco = res.data
          this.getBookBaseInfo()
        } else {
          this.$message({ message: res.msg, type: 'warning' })
        }
      }
    },
    created() {
      this.getBookBaseInfo()
  }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
.book-property-wrap
  .class-icon
    max-width 100%
    margin-bottom 15px
  .book-property-list
    .el-button
      margin-left 0!important
      margin-right: 15px;
      margin-bottom 15px
      .el-tag__close:hover
        background #ff4d51
        color #fff
  .property-ruleForm
     img
        max-height 100px
        margin-bottom 0
.tab-title
  font-size 22px
  line-height 40px
  margin-bottom  20px
  

.button-new-tag
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;

.input-new-tag
  display inline-block
  width: 90px;
  margin-left: 10px;
  
</style>



