          </el-form-item>
        </el-col>
    
        <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
          <el-form-item label="" prop="userCreateDate">
            <el-date-picker
              v-model="userDetailInfo.userCreateDate"
              type="datetime"
              readonly
              value-format="yyyy-MM-dd HH:mm:ss"
              placeholder="">
