        </el-col>
      </el-row>
      
      <el-row :gutter="40">
        <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
          <el-form-item label="" prop="lastLogTime">
            <el-date-picker
              v-model="userDetailInfo.lastLogTime"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              placeholder="">
            </el-date-picker>
