          </el-form-item>
        </el-col>
      </el-row>
  
      <el-row :gutter="40">
        <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
          <el-form-item label="" prop="userAutograph">
           <el-input type="textarea" v-model="userDetailInfo.userAutograph"></el-input>
            <p><span class="fr">{{userDetailInfo.userAutograph?userDetailInfo.userAutograph.length:0}}/20</span></p>
          </el-form-item>
        </el-col>
      </el-row>
      
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
      </el-form-item>
    </el-form>
