<template>
<div class="book-property-wrap">

    <el-alert title="" type="info" show-icon>
        <div>
            <p>"+ New Tag"""</p>
            <p><span class="red"></span>     </p>
            <p><span></span>:@B@- #@*&</p>
            <p><span></span> </p>
        </div>
    </el-alert>

    <!---->
    <el-row class="tag-row">
        <p class="tag-title"></p>
        <el-tag class="tag-item" :key="tag" v-for="tag in sensitiveWordsList" closable @close="handleDeleteWord('', tag)">{{tag}}</el-tag>
        <el-input
            class="tag-input"
            v-if="sensitiveWordsVisible"
            v-model="sensitiveWordsValue"
            ref="sensitiveWordsValue"
            @keyup.enter.native="handleAddWord('')"
            @blur="handleAddWord('')">
        </el-input>
        <el-button v-else class="tag-button" size="small" @click="showInput('')">+ New Tag</el-button>
    </el-row>

    <!---->
    <el-row class="tag-row">
        <p class="tag-title"></p>
        <el-tag class="tag-item":key="tag" v-for="tag in stopWordsList" closable @close="handleDeleteWord('', tag)">{{tag}}</el-tag>
        <el-input
            class="tag-input"
            v-if="stopWordsVisible"
            v-model="stopWordsValue"
            ref="stopWordsValue"
            @keyup.enter.native="handleAddWord('')"
            @blur="handleAddWord('')">
        </el-input>
        <el-button v-else class="tag-button" size="small" @click="showInput('')">+ New Tag</el-button>
    </el-row>

    <!---->
    <el-row class="tag-row">
        <p class="tag-title"></p>
        <el-tag class="tag-item" :key="tag" v-for="tag in warningWordsList" closable @close="handleDeleteWord('', tag)">{{tag}}</el-tag>
        <el-input
            class="tag-input"
            v-if="warningWordsVisible"
            v-model="warningWordsValue"
            ref="warningWordsValue"
            @keyup.enter.native="handleAddWord('')"
            @blur="handleAddWord('')">
        </el-input>
        <el-button v-else class="tag-button" size="small" @click="showInput('')">+ New Tag</el-button>
    </el-row>

</div>
</template>

<script>
export default {
  data() {
    return {
      sensitiveWordsList: [],
      sensitiveWordsVisible: false,
      sensitiveWordsValue: '',

      stopWordsList: [],
      stopWordsVisible: false,
      stopWordsValue: '',

      warningWordsList: [],
      warningWordsVisible: false,
      warningWordsValue: '',
      responseWord: function() {
        this.$store.dispatch('getSensitiveWords').then(res => {
          this.sensitiveWordsList = res.sensitiveWords
          this.stopWordsList = res.stopWordsList
          this.warningWordsList = res.warningWords
        })
      },
            // 
      contains: function(arr, obj) {
        var i = arr.length
        while (i--) {
          if (arr[i] === obj) return true
        }
        return false
      }

    }
  },
  methods: {

    handleAddWord(str) {
      if (str === '') {
        const word = this.sensitiveWordsValue
        if (word) {
          if (this.contains(this.sensitiveWordsList, word)) { this.$message({ message: '', type: 'warning' }) } else {
            this.$store.dispatch('changeSensitiveWords', { type: '1', world: word })
                        .then(res => {
                          this.responseWord()
                          this.$message({ message: '', type: 'success' })
                          this.sensitiveWordsVisible = false
                          this.sensitiveWordsValue = ''
                        })
          }
        } else this.sensitiveWordsVisible = false
      } else if (str === '') {
        const word = this.stopWordsValue
        if (word) {
          if (this.contains(this.stopWordsList, word)) { this.$message({ message: '', type: 'warning' }) } else {
            this.$store.dispatch('changeSensitiveWords', { type: '2', world: word })
                        .then(res => {
                          this.responseWord()
                          this.$message({ message: '', type: 'success' })
                          this.stopWordsVisible = false
                          this.stopWordsValue = ''
                        })
          }
        } else this.stopWordsVisible = false
      } else if (str === '') {
        const word = this.warningWordsValue
        if (word) {
          if (this.contains(this.warningWordsList, word)) { this.$message({ message: '', type: 'warning' }) } else {
            this.$store.dispatch('changeSensitiveWords', { type: '3', world: word })
                        .then(res => {
                          this.responseWord()
                          this.$message({ message: '', type: 'success' })
                          this.warningWordsVisible = false
                          this.warningWordsValue = ''
                        })
          }
        } else this.warningWordsVisible = false
      }
    },

    handleDeleteWord(str, tag) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        if (str === '') {
          this.$store.dispatch('changeSensitiveWords', { type: '4', world: tag }).then(res => {
            this.responseWord()
            this.$message({ message: '', type: 'success' })
            this.sensitiveWordsVisible = false
            this.sensitiveWordsValue = ''
          })
        } else if (str === '') {
          this.$store.dispatch('changeSensitiveWords', { type: '5', world: tag }).then(res => {
            this.responseWord()
            this.$message({ message: '', type: 'success' })
            this.stopWordsVisible = false
            this.stopWordsValue = ''
          })
        } else if (str === '') {
          this.$store.dispatch('changeSensitiveWords', { type: '6', world: tag }).then(res => {
            this.responseWord()
            this.$message({ message: '', type: 'success' })
            this.warningWordsVisible = false
            this.warningWordsValue = ''
          })
        }
      }).catch(() => {
        this.$message({ type: 'info', message: '' })
      })
    },

    showInput(str) {
      if (str === '') {
        this.sensitiveWordsVisible = true
        this.$nextTick(_ => {
          this.$refs.sensitiveWordsValue.$refs.input.focus()
        })
      } else if (str === '') {
        this.stopWordsVisible = true
        this.$nextTick(_ => {
          this.$refs.stopWordsValue.$refs.input.focus()
        })
      } else if (str === '') {
        this.warningWordsVisible = true
        this.$nextTick(_ => {
          this.$refs.warningWordsValue.$refs.input.focus()
        })
      }
    }

  },
  created() {
    this.responseWord()
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.tag-row
    margin 70px 0
    .tag-title
        font-size 18px
        font-weight bold
        margin-bottom 20px
    .tag-item
        margin-right 10px
    .tag-input
        width 90px
        margin-left 10px
        vertical-align bottom
        input
            height 32px

    .tag-button
        margin-left 10px
        height 32px
        line-height 30px
        padding-top 0
        padding-bottom 0
</style>



